    vivod = () => {
        if (this.state.userFull.balance_bitcoin >= 50 && this.state.userFull.balance_rub >= 100) {
            this.setState({
                errorNew: ''
            });

            if (this.state.summ >= 100) {
                if (this.state.summ <= 15000) {
                    if (this.state.summ <= this.state.userFull.balance_rub) {
                        this.api.Get('api.users.pay_out', '&sum=' + this.state.summ + '&type=' + this.state.type + '&wallet=' + this.state.v)
                            .then(response => {
                                if (response.status === 'ok') {

                                    this.setState({
                                        userFull: {
                                            balance_bitcoin: response.user_info.balance_bitcoin,
                                            balance_bizcoin: response.user_info.balance_bizcoin,
                                            balance_rub: response.user_info.balance,
                                            last_boost_time: response.user_info.last_boost_time,
                                            activity: response.user_info.activity,
                                            user_performance: response.user_info.user_performance,
                                            id: response.user_info.user_id,
                                            last_bonus_time: response.user_info.last_bonus_time,
                                        }
                                    });
                                    this.onModalChange(null, null);
                                    this.openDoneSnackBar('Выплата отправлена на модерацию', null);
                                } else {
                                    if (response.error) {
                                        if (response.code === 15) {
                                            this.setState({
                                                errorNew: 'Вывод разрешен 1 раз в сутки'
                                            });
                                        } else if (response.code === 14) {
                                            this.setState({
                                                errorNew: 'Не достаточно средств 💷 для вывода'
                                            });
                                            this.onModalChange(null, 'balance2')
                                        } else {
                                            this.setState({
                                                errorNew: response.error
                                            });
                                        }

                                    }
                                    console.error(response);
                                }

                                console.log(response);

                            }).catch(error => {
                            console.log(error);
                        });
                    } else {
                        this.setState({
                            errorNew: 'На балансе недостаточно средств для вывода'
                        });
                    }
                } else {
                    this.setState({
                        errorNew: 'Сумма вывода до 15 000 р.'
                    });
                }
            } else {
                this.setState({
                    errorNew: 'Сумма вывода от 100 р.'
                });
            }
        } else {
            this.onModalChange(null, 'balance2')
        }
    };

    TIMER = () => {
        if (this.state.userFull.last_bonus_time) {
            if (this.state.time > this.state.userFull.last_bonus_time) {
                let tt = 86400 - (this.state.time - this.state.userFull.last_bonus_time);
                console.log(tt);
                if (tt > 0) {
                    let deadline = new Date(Date.parse(new Date(this.state.time * 1000)) + tt * 1000);
                    console.log(this.state.time);
                    console.log(this.state.userFull.last_bonus_time);

                    this.setState({
                        timerOn: true
                    });


                    let timeinterval = setInterval(() => {
                        let t = getTimeRemaining(deadline);
                        this.setState({
                            timer: {
                                h: ('0' + t.hours).slice(-2),
                                m: ('0' + t.minutes).slice(-2),
                                s: ('0' + t.seconds).slice(-2)
                            }
                        });
                        if (t.total <= 0) {
                            clearInterval(timeinterval);
                        }
                        //console.log(this.state.timer);
                    }, 1000);
                }
            }
        } else {
            console.log("Открывай пак скорее");
        }
    };

