    onStoryChange = e => {
        window.scrollTo(0, 0);

        let stor = e.currentTarget.dataset.story;
        let idS = e.currentTarget.dataset.story.replace('article-', '');
        if (parseInt(idS) > 0) {
            this.getPost(idS);
            stor = 'article';

        }

        if (e.currentTarget.dataset.story === 'galley') {
            this.getPhoto();
        } else if (e.currentTarget.dataset.story === 'home') {
            this.getDayPhoto();
            this.getHome();
        } else if (e.currentTarget.dataset.story === 'shop') {
            this.getTovar();
        } else if (e.currentTarget.dataset.story === 'articles') {
            this.getHome();
        }

        //window.location.pathname = e.currentTarget.dataset.story;
        if (e.currentTarget.dataset.story !== 'home') {
            window.history.pushState({panel: e.currentTarget.dataset.story}, e.currentTarget.dataset.story, '/' + e.currentTarget.dataset.story);
        } else {
            window.history.pushState({panel: 'home'}, 'home','/');
        }
        this.setState({
            activeStory: stor,
            offset: 0,
            typePost: 'text'
        })
    };

