    webSGame = () => {
        // this.webS();
        // bridge.send("VKWebAppTapticSelectionChanged", {});
        setTimeout(() => {
            this.setState({
                rotate: ''
            });
        }, 1000);
        this.setState({
            rotate: 'rot'
        });

        if (!this.state.gameLoad) {
            if (this.state.connection) {
                this.setState({
                    gameLoad: true,
                    codeWin: ''
                });
                let data = {
                    "action": "gamestart",
                    "status": "start_game"
                };
                let num = 4;
                let inn = setInterval(() => {
                    if (num) {
                        this.setState({
                            codeWin: this.state.codeWin + randomInteger(0, 9)
                        });

                    } else {
                        clearInterval(inn);

                        this.logWb('Send ', data);
                        this.state.connection.send(JSON.stringify(data));
                    }
                    num--;
                }, 300);


            } else {
                //this.openErrorSnackBar('Ошибка: WebSocket не подключен', null);
            }
        }

    };

    webS = async () => {
        let url = this.state.urlWeb;

        this.logWb('','',true);
        this.logWb('Подключаюсь к: ',url);
        this.setState({
            connection: await new WebSocket(url)
        });
        //let connection = await new WebSocket(url);
        this.logWb('Открываю соединение...','');

        //opening the connection
        this.state.connection.onopen = function () {
            //connection.send('Ping'); //
        }.bind(this);


        this.state.connection.onerror = function (error) {

            this.logWb('WebSocket Error: ',JSON.stringify(error));
            //this.openErrorSnackBar('Ошибка: WebSocket Error',null);

            // setTimeout(()=>{
            //     this.setState({
            //         connection: null
            //     });
            //     this.webS();
            // },20000);


            return 1;
        }.bind(this);

        this.state.connection.onclose = function(event) {
            if (event.wasClean) {
                //alert('Соединение закрыто чисто');
            } else {
                //alert('Обрыв соединения'); // например, "убит" процесс сервера
            }
            if (!this.state.errorSocet) {
                // setTimeout(()=>{
                //     this.setState({
                //         connection: null
                //     });
                //     this.webS();
                // },20000);
            }
            //alert('Код: ' + event.code + ' причина: ' + event.reason);
            this.logWb('WebSocket Close: ',JSON.stringify(event));
        }.bind(this);

        //to receive the message from server
        this.state.connection.onmessage = function (e) {
            //console.log('Server: ' + e.data);
            // this.setState({
            //     textS: this.state.textS+'\nServer: ' + e.data
            // });
            let res = JSON.parse(e.data);
            if (res.error) {
                if (res.code === 6) {
                    this.setState({
                        errorSocet: true
                    });
                } else if (res.code === 5) {
                    this.setState({
                        gameLoad: false
                    });
                    this.openWinSnackBar(
                        'Не удалось',
                        <Avatar size={24} style={{backgroundColor: '#b3983b'}}>
                            <Icon16Recent fill="#fff" width={14} height={14}/>
                        </Avatar>
                    );
                    // bridge.send("VKWebAppTapticSelectionChanged", {});
                } else {
                    this.openErrorSnackBar('Ошибка: '+res.error,null);
                }

                this.logWb('Ошибка: ',res.error);
            } else if (res.status) {
                this.setState({
                    codeWin: '',
                    gameLoad: false
                });

                if (res.balance) {
                    let user = this.state.userFull;
                    user.balance_bizcoin = res.balance;
                    this.setState({
                        userFull: user,
                    });
                }


                if (res.status === 'connected') {
                    //let data = URLToArray(window.location.search);
                    let data = {
                        link: window.location.search
                    };
                    this.logWb('Ответ: ',res.status);
                    this.logWb('Отправляю: ',JSON.stringify(data));
                    console.log(JSON.stringify(data));
                    this.state.connection.send(JSON.stringify(data));
                } else if (res.status === 'game_started') {

                    this.logWb('Ответ: ',JSON.stringify(res));
                } else if (res.status ==='win') {
                    this.openWinSnackBar(
                        'Вам выпало '+res.to_plus+' поездок',
                        <Avatar size={24} style={{backgroundColor: '#4bb34b'}}>
                            <Icon16Done fill="#fff" width={14} height={14}/>
                        </Avatar>
                    );
                    // bridge.send("VKWebAppTapticNotificationOccurred", {"type": "success"});
                } else if (res.status ==='defeat') {
                    this.openWinSnackBar(
                        'У вас украли '+res.to_mines+' поездок',
                        <Avatar size={24} style={{backgroundColor: '#b3423c'}}>
                            <Icon16Clear fill="#fff" width={14} height={14}/>
                        </Avatar>
                    );
                    // bridge.send("VKWebAppTapticNotificationOccurred", {"type": "error"});
                } else if (res.status ==='none') {
                    this.openWinSnackBar(
                        'Не удалось',
                        <Avatar size={24} style={{backgroundColor: '#b3983b'}}>
                            <Icon16Recent fill="#fff" width={14} height={14}/>
                        </Avatar>
                    );
                    // bridge.send("VKWebAppTapticSelectionChanged", {});
                } else {
                    this.logWb('Неизвестный ответ: ',JSON.stringify(res));
                }


            } else if (res.check_sign_status) {
                if (res.check_sign_status === 'OK') {
                    this.logWb('Ответ подписи: ',res.check_sign_status);
                    // let data = {
                    //     "action": "gamestart",
                    //     "status": "start_game"
                    // };
                    // console.log(JSON.stringify(data));
                    // this.logWb('Отправляю: ',JSON.stringify(data));
                    // this.state.connection.send(JSON.stringify(data));
                }
            } else {
                this.logWb('Неизвестный ответ: ',res);
            }
        }.bind(this);


    };


